<template>
  <div class="panel panel-default">
    <div class="panel-heading">Penel heading witout title</div>
    <div class="panel-body">Panel content</div>
    <button  @click="goHome">클릭</button>
  </div>
</template>

<script>
export default {
  name: "Example",
  data() {
    return {
      msg: "Basic panel example"
    };
  },
  methods: {
    goHome: function () {
      this.$router.push({
        name: 'HelloWorld'
      
      })
    },
    abc (value) {
      alert(value)
    }
  }
};
</script>


